.about h1{
    font-size: 70px;
    text-align: center;
    font-weight: 700;
}
.aboutpara{
    font-size: 22px;
    padding: 0px 10px;
    text-align: justify;
}
.about{
    margin-top: 100px;
}

/* contact  */
.contactimage{
    display: flex;
    justify-content: center;
    align-items: center;
}
.contact-box{
    display: flex;
    justify-content: center;
    align-items: center;
}
.contact h1,p{
    text-align: center;
}

/* home page  */

.products .row {
    row-gap: 20px;
    margin: 20px 0px;
}
.products{
    margin-top: 14vw !important;
}

.productimage{
    height: 21vw;
    object-fit: cover;
    object-position: center;
}
.productimage img{
    object-fit: cover;
    object-position: center;
}
.card{
    height: 36vw;
    margin: 20px 0px;
    transition: 200ms ease-in all;
}
.card-body{
    display: flex;
    justify-content: space-between;
    flex-direction: column;

}
.card:hover{
    transform: scale(1.04);
    box-shadow: 0 0 10px rgb(203, 203, 203);
}
@media screen and  (max-width:800px) {
    .shoppage button{
        font-size: 11px;
        padding: 0px 5px !important;
        
    }
    .card {
        /* background: aquamarine; */
        height: 73vw;
        margin: 20px 0px;
    }
    .productimage {
        height: 41vw;
        object-fit: cover;
        object-position: center;
    }
    .card-title{
        font-size: 3.5vw;
    }
    .card-body{
        padding: 5px;
    }
    .productbtn
    {
        font-size: 2.5vw;
        /* flex-direction: column; */
    }
    .navbar-toggler {
        filter: invert(1);
    }
    .products{
        margin-top: 150px !important;
    }
}