
.navbar{
    background: linear-gradient(158deg, rgb(0, 0, 0) 0%, rgb(3, 44, 90) 100%);

}
.navbar-brand{
  /* color: white; */
    font-size: 30px;
    margin-left: 20px;
    background: linear-gradient(90deg, rgb(255, 127, 221) 5%, rgb(185, 129, 249) 100%);
