}
.signupbtn{
    margin-left: 20px;
    padding: 0px 42px;
    font-size: 21px;
    font-weight: 700;
    
    transition:  200ms ease all;
}
.signupbtn:hover{
    transform: scale(1.05);
}

.cart{
    color: white;
    filter: invert(1);
    width: 27px;
    margin-top: -7px;
}
.carti{
    position: relative;
}
.cartvalue{
    position: absolute;
    top: 0;
    right: 0;
    font-size: 12px;
    background: crimson;
    display: flex;
    justify-content: center;
    align-items: center;
    border-radius: 50%;
    width: 20px;
    height: 20px;

}
.cartvalue:hover{
    color: white !important;
}
.carti a:hover{
    color: white !important;
}
.button-75 {
    margin-left: 30px;
    align-items: center;
    background-image: linear-gradient(135deg, #f34079 40%, #fc894d);
    border: 0;
    border-radius: 10px;
    box-sizing: border-box;
    color: #fff;
    cursor: pointer;
    display: flex;
    flex-direction: column;
    font-family: "Codec cold",sans-serif;
    font-size: 16px;
    font-weight: 700;
    height: 54px;
    justify-content: center;
    letter-spacing: .4px;
    line-height: 1;
    max-width: 100%;
    padding-left: 20px;
    padding-right: 20px;
    padding-top: 3px;
    text-decoration: none;
    text-transform: uppercase;
    user-select: none;
    -webkit-user-select: none;
    touch-action: manipulation;
  }
  
  .button-75:active {
    outline: 0;
  }
  
  .button-75:hover {
    outline: 0;
  }
  
  .button-75 span {
    transition: all 200ms;
  }
  
  .button-75:hover span {
    transform: scale(.9);
    opacity: .75;
  }
  
  @media screen and (max-width: 991px) {
    .button-75 {
      font-size: 15px;
      height: 50px;
    }
  
    .button-75 span {
      line-height: 50px;
    }
  }