.productlargeimage{
    object-fit: cover;
}
.productlarge{
    display: none;
    position: absolute;
    left: 0;
    top: 0%;
    background-color: black;
    height: 100vh;
    z-index: 1000;
    width: 100%;
    /* display: flex; */
    justify-content: center;
    align-items: center;
}
.closeimagebtn{
    background: transparent;
    color: white;
    border: 3px solid white;
    padding: 8px 15px;
    border-radius: 27px;
    position: relative;
    right: -17%;
    top: -42%;
    cursor: pointer;
}
.closeimagebtn:hover{
background-color: white !important;
color: black !important;
}
.imagecursor{
    cursor: zoom-in;
}